import { Navbar, Button, Nav } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import styled from "styled-components";

import Logo from '../Logo';
import './styles.scss';
import {useWalletModalToggle} from "../../state/application/hooks";
import {useActiveWeb3React} from "../../hooks";
import {getEtherscanLink, shortenAddress} from "../../utils";
import UserIcon from "../Icons/User";


const WalletLink = styled.a`
  color: ${({ theme }) => theme.primary};
  background-color: rgba(71, 183, 254, 0.15);
  border-radius: 18px;
  text-decoration: none;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  font-size: 1rem;
  max-height: 48px;
  height: 48px;
  font-weight: 500;
  margin-bottom: 0;
  cursor: pointer;
  margin-right: 10px;
  
  :hover {
    text-decoration: none;
   
  }
  
  :focus,
  :active {
      text-decoration: none;
      outline: none;
  }
`


const Header = (props) => {
    const { account, chainId } = useActiveWeb3React()
    const toggleConnectModal = useWalletModalToggle();

    return (
        <Navbar className={'header'} expand={'lg'} variant={'dark'}>
            <div className={'header__inner'}>
                <Navbar.Brand>
                    <Logo />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
            </div>
            <Navbar.Collapse id="basic-navbar-nav" >
                <Nav className="mr-auto header__nav">
                    <NavLink to={'/fiat-ramp'} className={'header__item'} activeClassName={'header__item--active'}>Fiat Ramp</NavLink>
                    <NavLink to={'/markets'} className={'header__item'} activeClassName={'header__item--active'}>Markets</NavLink>
                    <NavLink to={'/trading'} className={'header__item'} activeClassName={'header__item--active'}>Trading</NavLink>
                    <NavLink to={'/invest'} className={'header__item'} activeClassName={'header__item--active'}>Invest</NavLink>
                    <NavLink to={'/tools'} className={'header__item'} activeClassName={'header__item--active'}>Tools</NavLink>
                </Nav>
                <div className={'d-flex align-items-stretch align-items-lg-center flex-column flex-lg-row'}>
                    {!account ? (
                        <Button variant={'outline-primary'} onClick={toggleConnectModal}>Connect</Button>
                    ) : (
                        <div className={'d-flex align-items-center justify-content-center pt-3 pt-lg-0'}>
                            <WalletLink
                                href={chainId && getEtherscanLink(chainId, account, 'address')}
                                target={"_blank"}
                                rel={'noopener noreferrer'}
                            >{ shortenAddress(account) }</WalletLink>
                            <button className="btn btn-circle btn-light-primary" onClick={toggleConnectModal}>
                                <UserIcon size={'20px'}/>
                            </button>
                        </div>
                    )}
                </div>
            </Navbar.Collapse>
        </Navbar>
    )
}

export default Header;