import React, { Component } from "react";
import styled, { ThemeContext } from 'styled-components';

import Card from "../../components/Card";
import {Nav, Tab, Row, InputGroup, FormControl, Col} from "react-bootstrap";
import PoolsTab from "./PoolsTab";
import { connect } from 'react-redux';
import Button from "../../components/UI/Button";
import SearchIcon from "../../assets/images/search.svg";


const CustomTitle = styled.span`
  font-weight: 700;
  color: ${({ theme }) => theme.text1};
  font-size: 1.25rem;
  margin-bottom: .5rem;
`

const CustomSubtitle = styled.span`
  font-size: 1rem;
  font-weight: 500;
  color: white;
  opacity: 0.5;
`

const Content = styled.div`
  padding-top: 57px;
`

const CustomNav = styled(Nav)`
  margin-left: -10px !important;
  margin-right: -10px !important;
  margin-bottom: 30px;
`

const CustomNavItem = styled(Nav.Item)`
  padding: 0 10px 10px;
`

const CustomNavLink = styled(Nav.Link)`
  border-radius: 18px !important;
  color: ${({ theme }) => theme.primary};
  background-color: rgba(71, 183, 254, 0.15);
  padding: 14px 24px;
  min-height: 56px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  
  &:hover {
    color: ${({ theme }) => theme.primary};
  }
  
  &.active {
    color: white;
    background-color: ${({ theme }) => theme.primary};
  }
`

class PoolsCard extends Component {
    static contextType = ThemeContext

    state = {
        query: '',
    }

    changeInputHandler = (e) => {
        this.setState({
            query: e.target.value,
        })
    }
    render() {
        const theme = this.context;
        return (
            <Card className={`gutter-b`}>
                <div className="d-flex flex-column flex-lg-row align-items-stretch align-items-lg-center justify-content-between border-0">
                    <div className="d-flex justify-content-center align-items-start flex-column">
                        <CustomTitle>
                            Explore Opportunities
                        </CustomTitle>
                        <CustomSubtitle>
                            Add liquidity to earn fees, incentives, voting rights, etc.
                        </CustomSubtitle>
                    </div>
                    <div className="card-toolbar d-flex flex-column-reverse flex-lg-row align-items-stretch align-items-lg-center justify-content-end pt-4 pt-lg-0">
                        <Button
                            style={{ minWidth: 127 }}
                            onClick={this.props.investHandler}
                            className={'mr-0 mr-lg-40'}
                        >
                            Invest
                        </Button>

                        <InputGroup className={'w-auto mb-3 mb-lg-0'}>
                            <InputGroup.Prepend>
                                <InputGroup.Text>
                                    <img src={SearchIcon} alt="search"/>
                                </InputGroup.Text>
                            </InputGroup.Prepend>
                            <FormControl id="PoolsSearch" placeholder="Search" onChange={this.changeInputHandler}/>
                        </InputGroup>
                    </div>
                </div>
                <Content>
                    <Tab.Container defaultActiveKey={'Uniswap'}>
                        <Row>
                            <Col xs={12}>
                                <CustomNav fill variant="pills" className={'d-flex flex-column flex-lg-row align-items-stretch align-items-lg-center'} >
                                    <CustomNavItem className={'flex-grow-1'}>
                                        <CustomNavLink eventKey="Uniswap">Uniswap</CustomNavLink>
                                    </CustomNavItem>
                                    <CustomNavItem className={'flex-grow-1'}>
                                        <CustomNavLink eventKey="Balancer">Balancer</CustomNavLink>
                                    </CustomNavItem>
                                    <CustomNavItem className={'flex-grow-1'}>
                                        <CustomNavLink eventKey="Curve">Curve</CustomNavLink>
                                    </CustomNavItem>
                                    <CustomNavItem className={'flex-grow-1'}>
                                        <CustomNavLink eventKey="Yearn">yEarn</CustomNavLink>
                                    </CustomNavItem>
                                </CustomNav>
                            </Col>
                            <Col xs={12}>
                                <Tab.Content className={'bg-transparent'}>
                                    <Tab.Pane eventKey="Uniswap">
                                        <PoolsTab type={'Uniswap'} addLiquidityHandler={this.props.addLiquidityHandler} removeLiquidityHandler={this.props.removeLiquidityHandler} query={this.state.query}/>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="Balancer">
                                        <PoolsTab type={'Balancer'} addLiquidityHandler={this.props.addLiquidityHandler} removeLiquidityHandler={this.props.removeLiquidityHandler} query={this.state.query}/>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="Curve">
                                        <PoolsTab type={'Curve'} addLiquidityHandler={this.props.addLiquidityHandler} removeLiquidityHandler={this.props.removeLiquidityHandler} query={this.state.query}/>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="Yearn">
                                        <PoolsTab type={'Yearn'} addLiquidityHandler={this.props.addLiquidityHandler} removeLiquidityHandler={this.props.removeLiquidityHandler} query={this.state.query}/>
                                    </Tab.Pane>
                                </Tab.Content>
                            </Col>
                        </Row>

                    </Tab.Container>
                </Content>
                {/*end::body*/}
            </Card>
        );
    }
}


const mapStateToProps = state => {
    return {
        darkMode: state.user.userDarkMode,
    }
}

export default connect(mapStateToProps)(PoolsCard)